<template>
    <div>
      <el-container>
        <el-header style="height: 5%"></el-header>
        <el-main style="display: flex; flex-direction: column">
          <div>
            <div
              style="
                display: flex;
                width: auto;
                flex-direction: column;
                align-items: center;
                justify-content: center;
              "
            >
              <el-input
                placeholder="请输入..."
                v-model="input3"
                style="width: auto"
              >
                <el-button
                  slot="append"
                  icon="el-icon-search"
                  @click="search"
                ></el-button>
              </el-input>
            </div>
          </div>
          <div style="height: 45px"></div>
          <div style="display: flex">
            <div style="width: 150px"></div>
            <div
              style="
                width: 250px;
                height: 750px;
                background: #ffffff;
                border-radius: 6px;
              "
            >
              <el-row
                class="tac"
                style="
                  width: auto;
                  height: 750px;
                  background: #ffffff;
                  border-radius: 6px;
                "
              >
                <el-col :span="24">
                  <el-menu default-active="3" style="line-height: 25px">
                    <div style="height: 10px"></div>
  
                    <el-menu-item index="1">
                      <i class="el-icon-picture-outline-round"></i>
                      <span slot="title" @click="hand1">道具专区</span>
                    </el-menu-item>
                    <el-menu-item index="2">
                      <i class="el-icon-mouse"></i>
                      <span slot="title" @click="hand2">宠物专区</span>
                    </el-menu-item>
  
                    <el-menu-item index="3">
                      <i class="el-icon-s-custom"></i>
                      <span slot="title" @click="hand3">账号专区</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                      <i class="el-icon-s-check"></i>
                      <span slot="title" @click="hand4">代练专区</span>
                    </el-menu-item>
                    <el-menu-item index="5">
                      <i class="el-icon-s-goods"></i>
                      <span slot="title" @click="hand5">求购专区</span>
                    </el-menu-item>
                    <el-menu-item index="6">
                      <i class="el-icon-setting"></i>
                      <span slot="title" @click="hand6">攻略专区</span>
                    </el-menu-item>
  
                    <el-menu-item index="7">
                      <i class="el-icon-setting"></i>
                      <span slot="title" @click="hand7">工具专区</span>
                    </el-menu-item>
                    <el-menu-item index="8">
                      <i class="el-icon-warning-outline"></i>
                      <span slot="title" @click="hand8">骗子专区</span>
                    </el-menu-item>
                  </el-menu>
  
                  <div style="display: flex; justify-content: center;">
    <div
      style="
        position: relative;
        width: 240px;
        height: 165px;
        overflow-y: hidden;
        overflow-y: hidden;
                        margin-top: -35px;
                        z-index: 2;
      "
    >
      <img
        src="@/assets/tian/11111.png"
        style="
          height: 100%;
          width: auto; /* 将宽度设置为自动，以保持原始比例 */
          position: relative;
          z-index: 2;
        "
      />
    </div>
  </div>
  
                </el-col>
              </el-row>
            </div>
            <div style="width: 60px"></div>
            <div
              style="
                top: 5%;
                right: 150px;
                height: auto;
                flex: 1;
                background: #ffffff;
                border-radius: 6px;
              "
            >
              <div style="height: 25px"></div>
              <div style="display: flex">
                <div style="width: 40px"></div>
  
                <div>
                 
                </div>
                <div style="width: 50%;"></div>
                <div style="flex: 1">
                  <div>
                    <el-button
                      type="primary"
                      round
                      @click="addui = true"
                      size="mini"
                      >添加</el-button
                    >
                    <el-button
                      type="warning"
                      size="mini"
                      round
                      @click="getHotRanking"
                      >热销排行</el-button
                    ><el-button-group>
                    <el-button
                      type="success"
                      size="mini"
                      round
                      @click="moren"
                      >默认排序</el-button
                    >
                      
                    <span class="kong" >
                      <span
                        type="text"
                        class="arrUp"
                        @click="moren22"
                        
                      ></span>
                      <span
                        type="text"
                        class="arrDown"
                        @click="daoxu"
                        
                      ></span>
                    </span></el-button-group>
                      <span>价格</span>
                      <el-input-number
                        style="z-index: 0;width: 70px"
                        v-model="number1"
                        :min="0"
                        :max="9999999999999999999"
                        label="最小值"
                        size="mini"
                        :controls="false"
                      ></el-input-number>
                      <span>-</span>
                      <el-input-number
                      style="z-index: 0; width: 70px"
                      v-model="number2"
                      :min="0"
                      :max="9999999999999999999"
                      label="最大值"
                      size="mini"
                      :controls="false"
                    ></el-input-number>
                    <el-button-group>
                      <el-button
                        type="info"
                        size="mini"
                        round
                        @click="handleCalculate"
                        >筛选</el-button
                      ><span class="kong">
                      <span
                        type="text"
                        class="arrUp"
                        @click="clickUp"
                        
                      ></span>
                      <span
                        type="text"
                        class="arrDown"
                        @click="clickDown"
                       
                      ></span>
                    </span></el-button-group>
  
                      
                  </div>
                 
                   
                  
  
                
                </div>
              </div>
              <div style="height: 20px"></div>
              <div style="display: flex">
                <div style="width: 55px"></div>
                <div
                  style="width: 100%; height: 600px; flex: 1; overflow-y: auto"
                >
                  <el-row :gutter="0">
                    <el-col
                      :span="5"
                      v-for="(cart, index) in carts.slice(0, 12)"
                      :key="cart.id"
                      :offset="index > 0 ? 1.8 : 0"
                    >
                      <el-card style="width: 100%; height: 100%">
                        <div style="padding: 0">
                          <div style="height: 50px; top: 0; left: 0; right: 0">
                            <img
                              :src="getFullImagePath(cart.image)"
                              @click="showPopup(getFullImagePath(cart.image))"
                              style="
                                cursor: pointer;
                                width: 100%;
                                height: 80px;
                                top: 0;
                              "
                            />
                            <div v-if="isPopupVisible" class="popup">
                              <div class="popup-content">
                                <img :src="popupPhotoUrl" class="popup-photo" />
                                <span class="close-btn" @click="closePopup"
                                  >&times;</span
                                >
                              </div>
                            </div>
                          </div>
                          <div style="height: 25px"></div>
                          <div>
                            <span>账号描述:</span> <span>{{ cart.name }}</span>
                          </div>
                          <span> 账号价格:</span><span>{{ cart.price }}</span>
  
                          <div class="bottom clearfix">
                            <div>
                              <span>联系QQ:</span><span>{{ cart.qq }}</span>
                            </div>
                            <el-button type="text" @click="cha(cart.id)"
                              >详情</el-button
                            >
                          </div>
                        </div>
                      </el-card>
                    </el-col>
                  </el-row>
                </div>
              </div>
              <div
                class="block"
                style="display: flex; flex-direction: column; margin-top: auto"
              >
                <el-pagination
                  layout=" total,prev, pager, next,jumper"
                  :total="parseInt(total)"
                  :page-size="pagesize"
                  :current-page="currentPage"
                  @size-change="handleSizeChange"
                  @current-change="handlePageChange"
                >
                </el-pagination>
              </div>
            </div>
            <div style="width: 110px"></div>
          </div>
          <el-dialog title="详情" :visible.sync="chaui">
            <el-form :model="chaxun">
              <el-form-item label="账号描述" style="width: 60%; font-size: 80px">
                <el-input v-model="chaxun.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="联系QQ" style="width: 60%; font-size: 80px">
                <el-input v-model="chaxun.qq" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="权重" style="width: 60%; font-size: 80px">
                <el-input v-model="chaxun.weight" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="账号价格" style="width: 60%; font-size: 80px">
                <el-input v-model="chaxun.price" autocomplete="off"></el-input>
              </el-form-item>
  
              <el-form-item label="照片">
                <!-- <img
    :src="getFullImagePath(this.chaxun.image)"
  
  /> -->
                <el-upload
                  class="avatar-uploader"
                  :show-file-list="false"
                  :before-upload="beforeAvatarUpload"
                  
                >
                  <el-button size="small" type="primary">点击上传</el-button>
                </el-upload>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="xiugai()">修改</el-button>
              <el-button @click="delet()">删除</el-button>
            </div>
          </el-dialog>
          <el-dialog title="添加" :visible.sync="addui">
            <el-form :model="ad">
              <el-form-item label="账号描述" style="width: 60%; font-size: 80px">
                <el-input v-model="ad.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="联系QQ" style="width: 60%; font-size: 80px">
                <el-input v-model="ad.qq" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="账号价格" style="width: 60%; font-size: 80px">
                <el-input v-model="ad.price" autocomplete="off"></el-input>
              </el-form-item>
  
              <el-form-item label="权重" style="width: 60%; font-size: 80px">
                <el-input v-model="ad.weight" autocomplete="off"></el-input>
              </el-form-item>
              <el-upload
                class="avatar-uploader"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload"
                
              >
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="add()">添加</el-button>
              <el-button @click="quxiao()">取消</el-button>
            </div>
          </el-dialog>
        </el-main>
      </el-container>
    </div>
  </template>
    <script>
  import ajax from "@/untils/axios";
  
  import { Message } from "element-ui";
  export default {
    data() {
      return {
        equipmentId: "",
        carts: [],
        chaxun: {},
        ad: {},
        prev: 0,
        next: 0,
        id: "",
        input3: "",
        input: "",
        activeName: "first",
        number1: null,
        number2: null,
        num1: null,
        num2: null,
        ishot: 0,
        cost:"",
        latest:0,
        isPopupVisible: false,
        chaui: false,
        addui: false,
        popupPhotoUrl: "", // 添加popupPhotoUrl属性
        currentPage: 1, //初始页
        pagesize: 12, //每页多少数据
        total: "",
      };
    },
    methods: {
      clickUp(){
          this.cost=1;
          this.latest=0;
          this.moren()
      },
      clickDown(){
        this.cost=0;
          this.latest=0;
          this.moren()
  
      },
      daoxu(){
          this.latest=1;
          this.cost=2;
          this.num1='';
        this.num2='';
          this.moren();
      },
     
      handleCalculate() {
        // alert(this.number1),
        // alert(this.number2),
        this.num1 = this.number1;
        this.num2 = this.number2;
       this.moren();
      },
      getHotRanking() {
       this.ishot=1;
       this.moren();
       this.ishot=0;
      },
  
      search() {
        this.input = this.input3;
        this.moren();
      },
      moren22(){
        this.latest=0;
        this.cost=2;
        this.num1='';
        this.num2='';
        this.moren();
      },
      moren(){
        ajax
          .get("/accountPage", {
            params: {
              BeginPrice: this.num1,
              EndPrice: this.num2,
              ishot: this.ishot,
              page: this.currentPage, // 将参数名改为 page
              pagesize: this.pagesize,
              name: this.input,
              cost:this.cost,
              latest:this.latest,
            },
          })
          .then((res) => {
            console.log(res);
            this.carts = res.data.data.rows;
          });
      },
      
      handlePageChange(page) {
        this.currentPage = page;
        console.log(this.currentPage);
        ajax
          .get("/accountPage", {
            params: {
              BeginPrice: this.num1,
              EndPrice: this.num2,
              ishot: this.ishot,
              page: this.currentPage, // 将参数名改为 page
              pagesize: this.pagesize,
              name: this.input,
              cost:this.cost,
              latest:this.latest,
            },
          })
          .then((res) => {
            console.log(res);
            this.carts = res.data.data.rows;
            this.total = res.data.data.total;
          })
          .catch((error) => {
            console.error("获取数据时出错:", error);
          });
      },
      handleSizeChange: function (size) {
        this.pagesize = size;
        console.log(this.pagesize);
      },
      getFullImagePath(image) {
        return require("@/assets/tian/" + image);
      },
  
      cha(e) {
        const id = parseInt(e);
        ajax.get(`/account/${id}`).then((res) => {
          console.log(res);
          this.chaxun = res.data.data;
          this.chaui = true;
        });
      },
      delet() {
        const id = parseInt(this.chaxun.id);
        ajax
          .delete(`/account/${id}`)
          .then((response) => {
            if (response.data.code === 1) {
              console.log("删除成功:", response.data);
              Message.success("删除成功");
              this.chaxun = {};
              location.reload();
            }
          })
          .catch((error) => {
            console.error("删除资源时出错：", error);
            Message.error("删除失败");
          });
      },
  
      xiugai() {
        this.chaui = false;
        const params = {
          name: this.chaxun.name,
          price: this.chaxun.price,
          qq: this.chaxun.qq,
          image: this.chaxun.image,
          weight: this.chaxun.weight,
          id: this.chaxun.id,
        };
  
        ajax
          .post("/account/update", null, {
            params: params,
          })
          .then((response) => {
            if (response.data.code === 1) {
              // 响应中的 code 为 1，表示上传成功
              console.log("修改成功:", response.data);
              Message.success("修改成功");
              this.chaxun = {};
              location.reload();
              Message.success("修改成功");
            } else {
              Message.error("修改失败: " + response.data.msg);
            }
          })
          .catch((error) => {
            // 捕获异常
            console.error("请求异常:", error);
            Message.error("修改失败: ");
          });
      },
      quxiao() {
        this.addui = false;
        this.ad = [];
      },
      add() {
        const params = {
          name: this.ad.name,
          price: this.ad.price,
          qq: this.ad.qq,
          image: this.ad.image,
          weight: this.ad.weight,
        };
  
        ajax
          .post("/account", null, {
            params: params,
          })
          .then((response) => {
            if (response.data.code === 1) {
              // 响应中的 code 为 1，表示上传成功
              console.log("添加成功:", response.data);
              Message.success("添加成功");
              this.ad = {};
              location.reload();
            } else {
              Message.error("添加失败: " + response.data.msg);
            }
          })
          .catch((error) => {
            // 捕获异常
            console.error("请求异常:", error);
            Message.error("添加失败");
          });
      },
  
      beforeAvatarUpload(file) {
        // 检查文件大小是否超过50MB
        const maxSize = 50 * 1024 * 1024; // 50MB
        if (file.size > maxSize) {
          Message.error("上传的文件大小不能超过50MB");
          return false; // 返回 false 阻止上传
        }
  
        // 检查文件格式是否为jpg或png
        const fileType = file.type;
        if (fileType !== "image/jpeg" && fileType !== "image/png") {
          Message.error("上传的文件格式必须为 JPG 或 PNG");
          return false; // 返回 false 阻止上传
        }
  
        // 设置设备ID为文件名
        this.equipmentId = file.name; // 可以根据文件名做适当的处理
  
        // 创建 FormData 对象存储文件和设备ID
        const formData = new FormData();
        // 将文件添加到 FormData
        formData.append("file", file);
        // 将设备ID作为文件名，以txt格式添加到 FormData
        formData.append("equipmentId", `${this.equipmentId}.txt`);
  
        // 使用 ajax 发送 POST 请求
        ajax
          .post("/uploadFile", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .then((response) => {
            // 处理响应
            if (response.data.code === 1) {
              // 响应中的 code 为 1，表示上传成功
              this.ad.image = response.data.data; // 将图片链接保存到 ad.image 中
              this.chaxun.image = response.data.data;
              console.log("上传成功:", response.data);
              Message.success("上传成功");
            } else {
              Message.error("上传失败: " + response.data.msg);
            }
          })
          .catch((error) => {
            // 捕获异常
            console.error("请求异常:", error);
            Message.error("上传失败");
          });
  
        // 返回 false 阻止 el-upload 默认的上传行为
        return false;
      },
  
      fetchData() {
        ajax.get("/account").then((res) => {
          console.log(res);
          this.carts = res.data.data;
        });
      },
      hand1() {
        this.$router.push("/home");
      },
      hand2() {
        this.$router.push("/pet01guan");
      },
      hand3() {
        this.$router.push("/accountguan");
      },
      hand4() {
        this.$router.push("/practiseguan");
      },
      hand5() {
        this.$router.push("/qiuguan");
      },
      hand6() {
        this.$router.push("/lueguan");
      },
      hand7() {
        this.$router.push("/toolguan");
      },
      hand8() {
        this.$router.push("/pianziguan");
      },
      hand11() {
        this.$router.push("/home");
      },
      hand12() {
        this.$router.push("/proop02guan");
      },
      hand13() {
        this.$router.push("/proop03guan");
      },
      showPopup(path) {
        // 点击图片时触发弹窗效果
        this.popupPhotoUrl = path; // 设置弹窗图片路径
        this.isPopupVisible = true; // 显示弹窗
      },
      closePopup() {
        // 关闭弹窗
        this.isPopupVisible = false;
      },
    },
    mounted() {
      this.handlePageChange(this.currentPage);
    },
  };
  import "@/assets/css/head.css";
  import "@/assets/css/imagebag.css";
  </script>
    <style>
  
  </style>